@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

body { font-size: 12px; line-height: 1.2; font-family: "Lato", sans-serif; }

a { text-decoration: none; color: inherit; }

* { transition: hover, 0.5s; }

.inner { width: 1170px; margin: 0 auto; position: relative; padding: 0 10px; }
@media only screen and (max-width: 1200px) { .inner { width: auto; } }

.click, .copyrights { font-size: 12px; font-weight: bold; text-transform: uppercase; color: #B5B5B5; cursor: pointer; }
.click:hover, .copyrights:hover { color: black; }
.click_line { border-bottom: 2px solid transparent; padding-bottom: 5px; }
.click_line.is-active, .click_line:hover { color: black; border-bottom: 2px solid #007AD9; }
.click_color { color: black; }

@media only screen and (max-width: 767px) { .header__nav { position: absolute; bottom: -180px; left: 0; width: 100%; background: white; z-index: 100; display: none; }
  .header__nav li { display: block; text-align: center; margin: 0 0 12px 0; } }
.header__nav.is-visible { display: block; }
@media only screen and (max-width: 1024px) { .header__logo { position: absolute; top: 0; left: 50%; transform: translateX(-50%); } }
@media only screen and (max-width: 767px) { .header__logo { top: -10px; } }
.header__top { height: 45px; line-height: 45px; *zoom: 1; }
.header__top:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 767px) { .header__top { display: none; } }
.header__top-left { float: left; }
.header__top-left ul { font-size: 0; }
.header__top-left li { display: inline-block; margin-right: 30px; }
.header__top-left li:last-of-type { margin-right: 0; }
.header__top-right { float: right; *zoom: 1; }
.header__top-right:after { content: " "; display: table; clear: both; }
.header__select-box { float: left; border-right: 1px solid #EDEDED; padding: 0 20px; position: relative; }
.header__select-box:last-of-type { border: none; padding-right: 0; }
.header__select-wrap { position: relative; }
.header__select-wrap select { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); opacity: 0; width: 100%; }
.header__select-result:after { display: inline-block; content: ""; background-image: url("../img/icons.png"); width: 7px; height: 4px; background-position: 0px -18px; vertical-align: middle; margin-left: 15px; }
.header__main { border-top: 1px solid #EDEDED; border-bottom: 1px solid #EDEDED; padding: 27px 0; }
.header__main .inner { display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap; }
@media only screen and (max-width: 1024px) { .header__main .inner { padding-top: 40px; } }
.header__bag a { display: inline-block; vertical-align: middle; }
.header__bag-result-wrap { display: inline-block; vertical-align: middle; border: 2px solid #007AD9; border-radius: 50%; width: 45px; height: 45px; position: relative; margin: 0 15px; }
.header__bag-result { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); font-size: 12px; font-weight: bold; }
.header__search { display: inline-block; vertical-align: middle; background-image: url("../img/icons.png"); width: 14px; height: 14px; background-position: 0px 0px; background-color: transparent; }
.header__burger { width: 40px; height: 40px; top: 15px; display: none; }
@media only screen and (max-width: 767px) { .header__burger { display: block; } }
.header__burger span { display: block; height: 4px; background: #B5B5B5; margin-top: 7px; }

.nav, .catalog__top ul { font-size: 0; }
.nav li, .catalog__top ul li { display: inline-block; margin-right: 50px; }
.nav li:last-of-type, .catalog__top ul li:last-of-type { margin-right: 0; }
.nav .colored, .catalog__top ul .colored { color: #FF3F13; }

.content { padding-top: 100px; }
.content h1 { font-size: 58px; color: #333; text-align: center; font-family: "Playfair Display", serif; }

.catalog { padding-top: 55px; padding-bottom: 50px; }
.catalog__top { margin-bottom: 50px; }
.catalog__top ul { text-align: center; }
.catalog__top ul li { display: inline-block; }
@media only screen and (max-width: 767px) { .catalog__top ul li { text-align: center; margin: 0 10px 10px 10px; } }
.catalog__box { display: flex; justify-content: space-between; align-items: stretch; flex-wrap: wrap; margin-bottom: 55px; }
@media only screen and (max-width: 1024px) { .catalog__box { justify-content: space-around; } }
.catalog__more-wrap { text-align: center; }
.catalog__more { background: transparent; }

.item { position: relative; text-align: center; margin-bottom: 30px; }
.item__img { display: block; }
.item__name { font-family: "Playfair Display", serif; font-size: 16px; margin-top: 25px; margin-bottom: 10px; }
.item__price { font-size: 16px; color: #B5B5B5; }
.item__old-price { text-decoration: line-through; display: inline-block; margin-right: 10px; }
.item__new-price { color: #FF3F13; }
.item__new { border-radius: 13px; color: white; position: absolute; left: 25px; top: 25px; padding: 8px; text-transform: uppercase; background-color: #1274d3; background-image: linear-gradient(to right, transparent 0%, rgba(255, 255, 255, 0.25) 99%, rgba(255, 255, 255, 0.25) 100%); }

.subscribe { background: url("../img/subscribe-bg.jpg") center center repeat; background-size: cover; padding: 65px 0; text-align: center; }
.subscribe__title { font-size: 12px; color: #35667A; font-weight: bold; text-transform: uppercase; }
.subscribe__subtitle { color: white; font-size: 38px; font-family: "Playfair Display", serif; margin: 25px 0; }
.subscribe form { position: relative; display: inline-block; }
.subscribe form input[type="email"] { border: 2px solid rgba(255, 255, 255, 0.3); height: 50px; line-height: 50px; background: transparent; border-radius: 25px; padding: 0 35px 0 25px; text-transform: uppercase; color: white; }
.subscribe form input[type="email"]::placeholder { color: white; text-transform: uppercase; }
.subscribe form input[type="submit"] { background: transparent; display: block; position: absolute; right: 19px; top: 19px; background-image: url("../img/icons.png"); width: 6px; height: 10px; background-position: -18px 0px; }

.footer .inner { display: flex; justify-content: space-between; align-items: center; flex-wrap: wrap; padding: 30px 10px; }
@media only screen and (max-width: 767px) { .footer .inner { justify-content: center; } }
@media only screen and (max-width: 1024px) { .footer__nav { display: none; } }
@media only screen and (max-width: 767px) { .footer__logo { display: none; } }

.copyrights { cursor: default; }
.copyrights:hover { color: #B5B5B5; }


